<template>
  <div class="order-alipay">
    <loading v-if="loading"></loading>
    <div class="form" v-html="content"></div>
  </div>
</template>

<script>
import Loading from './../components/Loading'
export default {
  name: "alipay",
  components: {
    Loading
  },
  data () {
    return {
      orderId: this.$route.query.orderId,
      content: '',
      loading: true
    }
  },
  mounted () {
    this.paySubmit()
  },
  methods: {
    paySubmit () {
      this.axios.post('/pay', {
        orderId: this.orderId,
        orderName:' Vue',
        amount: 0.01,//
        payType: 1 //12
      }).then((res) => {
        // contenthtmlsubmit
        // this.content = "<h1>jaja</h1>";
        this.content = res.content;
        setTimeout(() => {
          this.loading = false;
          // 
          document.forms[0].submit();
        }, 100);
      })
    }
  }
};
</script>

